<script>
import { position, nextStop, setMapView } from "./Store.js";
export let Haltestellen;        // receive 
export let input = "";          // return

let bims = {};
let dataListOptions = [];

// Eventhandler for Location Input
const handleInputField = (e) => {
  if (input.length === 0) {
    // empty input field -> show location based
    dataListOptions = $nextStop;
  } else {
    // user types and searches station
    let pattern = new RegExp(e.target.value, "i");
    let FilterHaltestellen = [];
    if (input.length > 3) {
      for (let i = 0; i < Haltestellen.length; i++) {
        if (pattern.test(Haltestellen[i].NAME)) {
          FilterHaltestellen = [...FilterHaltestellen, Haltestellen[i]];
        }
      }
      //let FilterHaltestellen = Haltestellen.filter(e => pattern.test(e.NAME));
      if (FilterHaltestellen.length < 10) {
        dataListOptions = FilterHaltestellen;
      } 
    }
  };
} 

// Eventhandler for Button
const handleButtonClick = (e) => {
  if (input.length !== 0) {
    let pattern = new RegExp(input);
    let matchedStop = Haltestellen.filter(e => pattern.test(e.NAME))[0];
    let pos = {
      latitude: matchedStop.LATITUDE,
      longitude: matchedStop.LONGITUDE,
      accuracy: undefined
    };
    setMapView(pos);
  }
}

</script>

<div>
  {#if !$position.enabled}
    <label for="location">Standort wird ermittelt...</label>
  {:else}
    <label for="location">Station auswÃ¤hlen oder suchen</label>
  {/if}
  <form>
    <input id="location" type="text" list="stations" placeholder="ðŸ” Station auswÃ¤hlen"
           bind:value={input} on:keyup={handleInputField} autocomplete="off" />
    <datalist id="stations">
      {#if !$position.enabled}
        <option value="Schottentor">ðŸšŠ Schottentor</option>
        <option value="Vinzenzgasse">ðŸšŠ Vinzenzgasse</option>
        <option value="Rosensteingasse">ðŸšŠ Rosensteingasse</option>
      {:else}
        {#if dataListOptions.length > 0}
          {#each dataListOptions as s, index (s.DIVA)}
            <option value={s.NAME}>ðŸšŠ {s.NAME}</option>
          {/each}
        {:else} 
          <option value="Schottentor">ðŸšŠ Schottentor</option>
          <option value="Vinzenzgasse">ðŸšŠ Vinzenzgasse</option>
          <option value="Rosensteingasse">ðŸšŠ Rosensteingasse</option>
        {/if}
      {/if}
    </datalist>
    <button type="submit" on:click|preventDefault={handleButtonClick}>Go</button>
  </form>
</div>


