<script>
import { position, setMapView, getLocation, display } from "./Store.js";
import { onMount } from "svelte";
export let input = "";
export let Haltestellen; 

// Create map and point to Stephansdom
const initMap = () => {
  let steffl = [48.20849, 16.37317];
  let url = "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png";
  let attr = 'Map data &copy; <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, <a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>';
  myMap = L.map("map");
  L.tileLayer(url, {attribution: attr, minZoom: 15, maxZoom: 18}).addTo(myMap);
  myMap.setView(steffl, 15);

  Haltestellen.map(e => {
    let myIcon = L.divIcon({className: "my-icon", html: '<div style="font-size:1.5rem">ðŸšŠ</div>' });
    let marker = L.marker([e.LATITUDE, e.LONGITUDE], {icon: myIcon}).addTo(myMap);
    marker._id = e.NAME;
    //marker.bindPopup(`${e.NAME}`);
    marker.on("click", (event) => {
      //input = event.target._popup._content;
      input = marker._id;
      display.update(d => "display: block");
    });
  }); 
}

// Location Button Handler
const locationButtonHandler = () => {
  let pos = {
    latitude: $position.latitude,
    longitude: $position.longitude,
    accuracy: $position.accuracy
  }
  setMapView(pos); 
  position.set(getLocation());
}

onMount(async() => {
  initMap();
});
</script>

<div id="map"></div>
<button type="button" id="find-location-button" on:click={locationButtonHandler}>ðŸ”Ž Location</button>

<style lang="scss">#map {
  height: 100vh;
  max-width: 100vw;
  z-index: 0;
  position: fixed;
  left: 0;
  top: 0;
  right: 0; }

#find-location-button {
  position: absolute;
  z-index: 10;
  background-color: #eeeeee;
  border: 1px solid #2a4365;
  color: #2a4365;
  bottom: 1rem;
  left: 1rem; }

.my-icon {
  font-size: 100px; }

/*# sourceMappingURL=Map.svelte.css.map */</style>


