<script>
import { display, fetchData } from "./Store.js";
export let Haltestellen;
export let selected = "";
let promise;
let pattern;
let fetchThisStation;

// Await xhr request
const awaitData = async (x) => {
  let data = await fetchData(x);
  return data; 
}

promise = awaitData("Hauptbahnhof");

$: {
  fetchThisStation = Haltestellen.filter(e => selected === e.NAME)[0];
  if (fetchThisStation !== undefined) {
    promise = awaitData(fetchThisStation);
  }   
}

// Close Button
const closeButtonHandler = () => {
  display.update(d => "display: none");
}
</script>

<div id="bim" style={$display}>
  <button type="button" class="close" on:click={closeButtonHandler}>X</button>
  <h3>{selected}</h3>
  {#await promise}
    <p>Fetching data...</p>
  {:then data}
    <table>
      <th>Line</th>
      <th>Direction</th>
      <th>[Min.]</th>   
      {#each data.lines as d}
        <tr>
          <td>{d.name}</td>
          <td>{d.towards}</td>
          <td>{d.departures[0]}, {d.departures[1]}</td>
        </tr>
      {/each}
    </table>
  {:catch error}
    <p>{error}</p>
  {/await}  
</div>


<style lang="scss">#bim {
  background-color: rgba(255, 255, 255, 0.8);
  max-width: 500px;
  height: 300px;
  z-index: 1000;
  margin: 0 auto;
  margin-top: 5rem;
  position: relative;
  padding: 1rem;
  display: block; }

.close {
  color: #c53030;
  font-weight: 700;
  background-color: transparent;
  width: 20px;
  float: right;
  margin-right: -1rem;
  margin-top: -0.5rem; }

